<template lang="html">
  <div class="">
    <h1 v-for="item in dataa.sites">{{item.Name}}:{{item.Url}}</h1>
  </div>
</template>

<script>
export default {
	data:function(){
		return{
			dataa:{
			 "sites": [
				  {
				   
				  }
			  ]
			}		
		}
	},
	mounted(){
		this.$http.get('./src/json/1.json').then((response) => {
		    // 响应成功回调
		    console.log(response.data);
		    this.dataa= response.data
		 }, (response) => {
		   // 响应错误回调
		    console.log('服务器请求失败');
		});
	}
}
</script>

